import React, { FC } from 'react'
import clsx from 'clsx'
import { DeniedIcon } from '../../icons/Denied'
import { Typography } from '../Typography'
import { TagProps } from './types'
import './Tag.scss'

const Tag: FC<TagProps> = ({
  id,
  className,
  text = '',
  label,
  value,
  isActive,
  onChange,
  onDeleteClick,
  withCloseBtn = false,
  typeOfCloseBtn = 'fill',
  optionWithCount,
}) => {
  const handleChange = (): void => {
    if (!withCloseBtn && onChange) {
      onChange()
    }
  }

  const handleDeleteClick = (): void => {
    if (onDeleteClick) {
      onDeleteClick(id)
    }
  }

  const isConditionalTagWithOnlyText =
    optionWithCount?.title && !optionWithCount?.count?.length

  const tagWithLabelValue = text.length > 0 && !label && !value

  if (isConditionalTagWithOnlyText) {
    return (
      <p
        className={clsx(
          'Typography',
          'TagNext',
          'Typography__Body',
          'TagNext__Text',
          className,
          (isActive || withCloseBtn) && `TagNext_Active`,
        )}
        onClick={handleChange}
      >
        {optionWithCount?.title}
      </p>
    )
  }

  return (
    // eslint-disable-next-line
    <div
      className={clsx(
        'TagNext',
        className,
        (isActive || withCloseBtn) && `TagNext_Active`,
      )}
      onClick={handleChange}
    >
      {label && value ? (
        <>
          <Typography className='TagNext__Label' text={label} />
          <Typography className='TagNext__Value' text={value} />
        </>
      ) : null}
      {tagWithLabelValue && (
        <Typography className='TagNext__Text' text={text} />
      )}
      {optionWithCount && (
        <p className={clsx('Typography', 'Typography__Body', 'TagNext__Text')}>
          <span>{optionWithCount.title}</span>
          {Boolean(Number(optionWithCount.count)) && (
            <sup
              className={clsx(
                'Typography',
                'Typography__Caption',
                'TagNext__TextCount',
              )}
            >
              {optionWithCount.count}
            </sup>
          )}
        </p>
      )}
      {withCloseBtn && (
        <DeniedIcon
          className='TagNext__Icon'
          color='transparent'
          fill='white'
          onClick={handleDeleteClick}
          type={typeOfCloseBtn}
        />
      )}
    </div>
  )
}

export default Tag
